<template>
  <div @click="handleHideDialog">
    <vLoginHeader />
    <div class="top">
      <div class="box">
        <p class="title">{{ patientName }}</p>
        <div class="elbtn" @click="toPdf"></div>
        <!-- <router-link :to="{ name: 'patientsReport', query: {id: this.$route.query.vcfId}}" tag="div" class="elbtn"></router-link> -->
        <!-- <div class="elbtn" @></div> -->
      </div>
    </div>
    <div class="query-details">
      <!--  -->
      <div class="block block1 horizontal-bar">
        <p class="title"><em></em><span>High attention</span></p>
        <div class="gene-tab tab">
          <div class="tit tit-fix"></div>
          <div
            class="context-fix"
            :class="{ 'no-data': Object.keys(this.highAttention).length <= 0 }"
          >
            <p
              v-if="Object.keys(this.highAttention).length <= 0"
              class="no-data"
            >
              
            </p>
          </div>
          <vue-scroll :ops="scrollOps">
            <div class="tit tit2">
              <p>
                <span>RS</span>
                <span></span>
                <span>Ref</span>
                <span>Alt</span>
                <span></span>
                <span>MAF</span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
              </p>
            </div>
            <ul class="context">
              <template v-if="Object.keys(this.highAttention).length > 0">
                <li v-for="(item, index) in this.highAttention" :key="index">
                  <p>{{ item[""] }}</p>
                  <p class="more" @click="toLineHandle(item[''])">
                    {{ item[""] }}
                  </p>
                  <p>{{ item["Ref"] }}</p>
                  <p>{{ item["Alt"] }}</p>
                  <p>{{ item[""] }}</p>
                  <p>{{ item["MAF()"] }}</p>
                  <p>{{ item[""] }}</p>
                  <p>{{ item[""] }}</p>
                  <p>
                    <!-- {{typeof item[''] === 'string' ? item[''] : item[''].join(',')}} -->
                    <span
                      @mouseenter="showXGJBHandle(item[''], $event)"
                      style="display: block"
                      v-if="typeof item[''] === 'string'"
                    >
                      {{ item[""] }}</span
                    >
                    <span
                      @mouseenter="showXGJBHandle(xgjbItem, $event)"
                      style="display: block"
                      v-else
                      v-for="(xgjbItem, xgjbIndex) in item['']"
                      :key="xgjbIndex"
                    >
                      {{ xgjbItem }}
                    </span>
                  </p>
                  <p>
                    <!-- {{typeof item[''] === 'string' ? item[''] : item[''].join(',')}} -->
                    <span
                      style="display: block"
                      v-if="typeof item[''] === 'string'"
                      >{{ item[""] }}</span
                    >
                    <span
                      style="display: block"
                      v-else
                      v-for="(lyItem, lyIndex) in item['']"
                      :key="lyIndex"
                    >
                      {{ lyItem }}
                    </span>
                  </p>
                  <p>{{ item[""] }}</p>
                  <p>{{ item[""] }}</p>
                  <p>
                    <span
                      style="display: block"
                      v-for="(ite, i) in item['']"
                      :key="i"
                      @mouseenter="
                        handleShowDialog(
                          ite.name,
                          index,
                          i,
                          'highAttention',
                          $event,
                          'variantPmid'
                        )
                      "
                      @click="handleHideDialog(ite)"
                      class="spn"
                    >
                      {{ ite.name
                      }}{{ Number(i) !== item[""].length - 1 ? "" : "" }}
                    </span>
                  </p>
                </li>
              </template>
              <template v-else>
                <li class="no-data"></li>
              </template>
            </ul>
          </vue-scroll>
        </div>
      </div>
      <!--  -->
      <div class="block block2 horizontal-bar">
        <p class="title"><em></em><span>Moderate attention</span></p>
        <div class="gene-tab tab">
          <div class="tit tit-fix"></div>
          <div class="context-fix"></div>
          <vue-scroll :ops="scrollOps">
            <div class="tit tit2">
              <p>
                <span>RS</span>
                <span></span>
                <span>Ref</span>
                <span>Alt</span>
                <span></span>
                <span>MAF</span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
              </p>
            </div>
            <ul class="context">
              <template v-if="Object.keys(this.middleAttention).length > 0">
                <li v-for="(item, index) in this.middleAttention" :key="index">
                  <p>{{ item[""] }}</p>
                  <p class="more" @click="toLineHandle(item[''])">
                    {{ item[""] }}
                  </p>
                  <p>{{ item["Ref"] }}</p>
                  <p>{{ item["Alt"] }}</p>
                  <p>{{ item[""] }}</p>
                  <p>{{ item["MAF()"] }}</p>
                  <p>{{ item[""] }}</p>
                  <p>{{ item[""] }}</p>
                  <p>
                    <!-- {{typeof item[''] === 'string' ? item[''] : item[''].join(',')}} -->
                    <span
                      @mouseenter="showXGJBHandle(item[''], $event)"
                      style="display: block"
                      v-if="typeof item[''] === 'string'"
                    >
                      {{ item[""] }}</span
                    >
                    <span
                      @mouseenter="showXGJBHandle(xgjbItem, $event)"
                      style="display: block"
                      v-else
                      v-for="(xgjbItem, xgjbIndex) in item['']"
                      :key="xgjbIndex"
                    >
                      {{ xgjbItem }}
                    </span>
                  </p>
                  <p>
                    <!-- {{typeof item[''] === 'string' ? item[''] : item[''].join(',')}} -->
                    <span
                      style="display: block"
                      v-if="typeof item[''] === 'string'"
                      >{{ item[""] }}</span
                    >
                    <span
                      style="display: block"
                      v-else
                      v-for="(lyItem, lyIndex) in item['']"
                      :key="lyIndex"
                    >
                      {{ lyItem }}
                    </span>
                  </p>
                  <p>{{ item[""] }}</p>
                  <p>{{ item[""] }}</p>
                  <p>
                    <template v-if="item[''].length <= 2">
                      <span
                        style="display: block"
                        v-for="(ite, i) in item['']"
                        :key="i"
                        @mouseenter="
                          handleShowDialog(
                            ite.name,
                            index,
                            i,
                            'middleAttention',
                            $event,
                            'variantPmid'
                          )
                        "
                        @click="handleHideDialog(ite)"
                        class="spn"
                      >
                        {{ ite.name
                        }}{{ Number(i) !== item[""].length - 1 ? "," : "" }}
                      </span>
                    </template>
                    <template v-else>
                      <span
                        v-for="(ite, i) in item['']"
                        :key="i"
                        @mouseenter="
                          handleShowDialog(
                            ite.name,
                            index,
                            i,
                            'middleAttention',
                            $event,
                            'variantPmid'
                          )
                        "
                        @click="handleHideDialog(ite)"
                        class="spn"
                      >
                        <template v-if="i < 2">
                          <span style="display: block">
                            {{ ite.name
                            }}{{
                              Number(i) !== item[""].length - 1 ? "" : ""
                            }}</span
                          >
                        </template>
                      </span>
                      <span
                        class="spn more"
                        @click="showMoreDialog(item[''])"
                        >>></span
                      >
                    </template>
                    <!-- <span v-for="(ite, i) in item['']" :key="i" @mouseenter="handleShowDialog(ite.name, index, i, 'middleAttention', $event)" @click="handleHideDialog" class="spn">
                      {{ite.name}}{{Number(i) !== (item[''].length - 1) ? ',' : ''}}
                    </span> -->
                  </p>
                </li>
              </template>
              <template v-else>
                <li class="no-data"></li>
              </template>
            </ul>
          </vue-scroll>
        </div>
      </div>
      <!--  -->
      <div class="block block3 horizontal-bar">
        <p class="title"><em></em><span>other</span></p>
        <div class="gene-tab tab">
          <div class="tit tit-fix"></div>
          <div class="context-fix"></div>
          <vue-scroll :ops="scrollOps">
            <div class="tit tit2">
              <p>
                <span>RS</span>
                <span></span>
                <span>Ref</span>
                <span>Alt</span>
                <span></span>
                <span>MAF</span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
              </p>
            </div>
            <ul class="context">
              <template v-if="Object.keys(this.otherAttention).length > 0">
                <li v-for="(item, index) in this.otherAttention" :key="index">
                  <p>{{ item[""] }}</p>
                  <p class="more" @click="toLineHandle(item[''])">
                    {{ item[""] }}
                  </p>
                  <p>{{ item["Ref"] }}</p>
                  <p>{{ item["Alt"] }}</p>
                  <p>{{ item[""] }}</p>
                  <p>{{ item["MAF()"] }}</p>
                  <p>{{ item[""] }}</p>
                  <p>{{ item[""] }}</p>
                  <p>{{ item[""] }}</p>
                  <p>{{ item[""] }}</p>
                </li>
              </template>
              <template v-else>
                <li class="no-data"></li>
              </template>
            </ul>
          </vue-scroll>
        </div>
      </div>
      <!--  -->
      <div class="block block4 references" v-if="searchLiterature.length > 0">
        <p class="title"><em></em><span>references</span></p>
        <div class="references-cont">
          <p v-for="(item, index) in searchLiterature" :key="index">
            <span>{{ index + 1 }}</span>{{ item }}
          </p>
        </div>
        <!-- <div class="description">
          [1] Ammann, A. J., Wara, D. W., Cowan, M. J., Barrett, D. J., Stiehm,
          E. R. The DiGeorge syndrome and the fetal alcohol syndrome. Am. J.
          Dis. Child. 136: 906-908, 1982. [2] Asamoto, H., Furuta, M. DiGeorge
          syndrome associated with glioma and two kinds of viral infection.
          (Letter) New Eng. J. Med. 296: 1235 only, 1977.
        </div> -->
      </div>
    </div>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="moreDialog"
      width="700px"
      append-to-body
      center
      class="eldialog more-dialog"
    >
      <span></span>
      <span
        v-for="(ite, i) in moreData"
        :key="i"
        @mouseenter="
          handleShowDialog(ite.name, 0, i, 'moreData', $event, 'variantPmid')
        "
        @click="handleHideDialog(ite)"
        class="spn"
      >
        {{ ite.name }}{{ Number(i) !== moreData.length - 1 ? "," : "" }}
      </span>
    </el-dialog>
    <!--  -->
    <InfoDialog :pos="InfoDiaPos" :data="dialogInfo" v-if="showDialog1" />
    <vFooter />
  </div>
</template>

<script>
import vLoginHeader from "@/components/common/vLoginHeader";
import vFooter from "@/components/common/vFooter";
import InfoDialog from "@/components/InfoDialog";
import * as Cookies from "tiny-cookie";
export default {
  components: {
    vLoginHeader,
    vFooter,
    InfoDialog,
  },
  data() {
    let loading = ""; //loading
    var number = 0;
    function startLoading() {
      //Element loading-start 
      number++;
      loading = Loading.service({
        lock: false,
        text: "...",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.3)",
      });
    }
    function endLoading() {
      //Element loading-close 
      number = 0;
      loading.close();
    }

    return {
      patientName: this.$route.query.patientName || "",
      allData: {},
      // 
      highAttention: {},
      // 
      middleAttention: {},
      // 
      otherAttention: {},
      scrollOps: {
        vuescroll: {
          mode: "native",
        },
        scrollPanel: {
          initialScrollY: false,
          initialScrollX: false,
          scrollingX: true,
          scrollingY: false,
        },
        // 
        rail: {
          background: "#f1f1f1",
          opacity: 1,
          size: "5px",
          specifyBorderRadius: "6px",
          gutterOfEnds: "100px",
        },
        // 
        bar: {
          background: "#959595",
          onlyShowBarOnScroll: true,
          showDelay: 0,
          keepShow: true,
          size: "11px",
        },
      },
      showDialog1: false,
      InfoDiaPos: {},
      dialogInfo: {},
      moreDialog: false,
      moreData: [],
      references: [], // 
      searchLiterature: [], //
    };
  },
  methods: {
    init() {
      this.$get(this.$Url.vcf.detail, {
        vcfId: this.$route.query.vcfId,
      })
        .then((res) => {
          let data = res.data;
          if (data) {
            let allData = JSON.parse(data);
            // this.highAttention = allData['']
            this.getWxInfo(allData[""], (d) => {
              this.highAttention = d;
            });

            // let middleAttention = allData['']
            this.getWxInfo(allData[""], (d) => {
              this.middleAttention = d;
            });

            this.otherAttention = allData[""];

            this.concatHandle();
          }
        })
        .catch((err) => {});
    },
    getWxInfo(data, func) {
      let keys = Object.keys(data);
      let values = Object.values(data);
      values.forEach((v) => {
        let wx = v[""];
        if (typeof wx !== "string") {
          let arr = [];
          for (let j in wx) {
            let child = wx[j].split(",");
            let cArr = [];
            for (let k in child) {
              if (child[k] !== "None") {
                cArr.push({
                  name: child[k],
                  abst: "",
                });
              }
            }
            child = cArr;
            arr.push(...child);
          }
          v[""] = arr;
        } else {
          let child = wx.split(",");
          let cArr = [];
          for (let i in child) {
            if (child[i] !== "None") {
              cArr.push({
                name: child[i],
                abst: "",
              });
            }
          }
          child = cArr;
          v[""] = child;
        }
        func && func(data);
      });
    },

    handleShowDialog(txt, index, i, sdata, $event, type) {
      if (txt === "-") {
        this.showDialog1 = false;
        return;
      }

      let abst = "";
      if (sdata === "moreData") {
        abst = this[sdata][i]["abst"];
      } else {
        abst = this[sdata][index][""][i]["abst"];
      }

      if (!abst && type != "variantPmid" && sdata === "moreData") {
        debugger;
        this.$get(this.$Url.query.genebyOmId, {
          omim_id: txt,
        })
          .then((res) => {
            if (res.status === 200) {
              let data = res.data;
              if (data) {
                this.dialogInfo = {
                  title: txt,
                  description:
                    data.gene_summary !== "" ? data.gene_summary : "",
                };
                if (sdata === "moreData") {
                  this.$set(
                    this[sdata][i],
                    "abst",
                    this.dialogInfo.description
                  );
                } else {
                  this.$set(
                    this[sdata][index][""][i],
                    "abst",
                    this.dialogInfo.description
                  );
                }

                this.showDialog1 = true;
                let width = document.body.clientWidth;
                let height = document.body.clientHeight;
                this.InfoDiaPos = {
                  pageX:
                    $event.pageX + 300 > width
                      ? $event.pageX - 380
                      : $event.pageX,
                  pageY:
                    $event.pageY + 300 > height
                      ? $event.pageY - 300
                      : $event.pageY,
                };
              }
            }
          })
          .catch((err) => {});
      } else if (type == "variantPmid") {
        /**
         * 
         */
        window.$.ajax({
          headers: {
            token: Cookies.get("token"),
          },
          url: this.$Url.query.literature,
          data: {
            literatureId: txt,
          },
          success: (res) => {
            console.log(res);
            this.dialogInfo = {
              title: txt,
              description: res.data.literatureData,
              type: "PMID:" + txt,
            };
            this.showDialog1 = true;
            let width = document.body.clientWidth;
            let height = document.body.clientHeight;
            this.InfoDiaPos = {
              pageX:
                $event.pageX + 300 > width ? $event.pageX - 380 : $event.pageX,
              pageY:
                $event.pageY + 300 > height ? $event.pageY - 300 : $event.pageY,
            };
          },
        });
      } else {
        this.dialogInfo = {
          title: txt,
          description: abst,
        };
        this.showDialog1 = true;
        let width = document.body.clientWidth;
        let height = document.body.clientHeight;
        this.InfoDiaPos = {
          pageX: $event.pageX + 300 > width ? $event.pageX - 380 : $event.pageX,
          pageY:
            $event.pageY + 300 > height ? $event.pageY - 300 : $event.pageY,
        };
      }
    },
    handleHideDialog(ite) {
      this.showDialog1 = false;
    },
    toPdf() {
      let formData = new FormData();
      formData.append("patientId", this.$route.query.patientId);
      let config = {
        headers: { "Content-Type": "multipart/form-data" },
        responseType: "arraybuffer",
      };
      this.$post(this.$Url.pdf.export, formData, config)
        .then((res) => {
          let dom = document.createElement("a");
          let data = res;
          let blob = new Blob([data], {
            type: "application/pdf;charset-UTF-8",
          });
          let fileName = this.patientName + ".pdf";
          if (blob.size < 100) {
            this.$message({
              type: "warning",
              duration: 2000,
              message: "",
            });
          } else {
            dom.download = fileName;
            dom.style.display = "none";
            dom.href = URL.createObjectURL(blob);
            document.body.appendChild(dom);
            dom.click();
            URL.revokeObjectURL(dom.href); // URL 
            document.body.removeChild(dom);
          }
        })
        .catch((err) => {});
    },
    // 
    showMoreDialog(item) {
      this.moreData = item;
      this.moreDialog = true;
    },

    /**
     * 
     */
    toLineHandle(item) {
      let arr = item.split(":");
      let openUrl = `http://genome.ucsc.edu/cgi-bin/hgTracks?db=hg38&lastVirtModeType=default&lastVirtModeExtraState=&virtModeType=default&virtMode=0&nonVirtPosition=&position=chr${arr[0]}%3A${arr[1]}`;
      window.open(openUrl, "_blank");
    },

    /**
     * 
     */
    showXGJBHandle(txt, $event) {
      /**
       * 
       */
      window.$.ajax({
        headers: {
          token: Cookies.get("token"),
        },
        url: this.$Url.query.diseaseInformation,
        data: {
          OMIM_id: txt,
        },
        success: (res) => {
          if (res.status === 200) {
            console.log("", res.data);
            let data = res.data;
            this.dialogInfo = {
              title: txt,
              description: data.Description || data.definition_orp || "",
              type: "OMIM:" + txt,
            };
            this.showDialog1 = true;
            let width = document.body.clientWidth;
            let height = document.body.clientHeight;
            this.InfoDiaPos = {
              pageX:
                $event.pageX + 300 > width ? $event.pageX - 380 : $event.pageX,
              pageY:
                $event.pageY + 300 > height ? $event.pageY - 300 : $event.pageY,
            };
          }
        },
        error: (err) => {},
      });
    },

    /**
     * 
     */
    concatHandle() {
      //   

      let highAttentionObj = Object.keys(this.highAttention).map((item) => {
        let highAttentionItem = this.highAttention[item][""].map(
          (fItem) => fItem.name
        );
        return highAttentionItem;
      });

      let middleAttentionObj = Object.keys(this.middleAttention).map((item) => {
        let middleAttentionItem = this.middleAttention[item][""].map(
          (fItem) => fItem.name
        );
        return middleAttentionItem;
      });

      let highAttentionArr = [].concat(...highAttentionObj);
      let middleAttentionArr = [].concat(...middleAttentionObj);
      this.references = highAttentionArr.concat(middleAttentionArr);
      this.references = this.references.filter((item) => item != "-");
      this.searchLiteratureHandle();
    },

    /**
     * 
     */
    searchLiteratureHandle() {
      if (this.references.length != 0) {
        this.references.forEach((item, index) => {
          if (index < 10) {
            this.viewLiteratureLayerHandle(item);
          }
        });
      }
      console.log(this.searchLiterature);
    },

    viewLiteratureLayerHandle(txt) {
      this.$get(this.$Url.query.literature, {
        literatureId: txt,
      }).then((res) => {
        if (res.status === 200) {
          this.searchLiterature.push(res.data.reference);
        }
      });
    },
  },
  mounted() {
    this.init();
  },
};
</script>

<style lang="scss" scoped>
@import "@/static/_common";
.top {
  position: relative;
  width: 100%;
  height: 160px;
  margin-bottom: 20px;
  background-color: $bgColor1;
  .box {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 1270px;
    height: 100%;
    margin: 0 auto;
    text-align: center;
    .title {
      flex: 1;
      padding-left: 20px;
      text-align: left;
      font-size: 28px;
      color: $fontColor11;
    }
    .elbtn {
      width: 160px;
      height: 48px;
      line-height: 48px;
      border-radius: 36px;
      margin-right: 20px;
      text-align: center;
      font-size: 18px;
      color: #fff;
      background-image: $bgGradient2;
      cursor: pointer;
    }
  }
}
.query-details {
  margin-bottom: 40px;
}
.horizontal-bar {
  .tab {
    width: 1128px;
    .tit {
      display: block;
      width: 100%;
      padding: 0;
      // overflow-x: auto;
      p {
        display: flex;
        width: calc(200%);
        padding: 0 35px;
        box-sizing: border-box;
      }
      &.tit-fix {
        position: absolute;
        width: calc(100% + 40px);
        margin-left: -20px;
      }
      &.tit2 {
        background: none;
      }
    }
    .context {
      display: block;
      width: calc(200%);
      border-color: transparent;
      li {
        background-color: transparent;
      }
    }
    .context-fix {
      position: absolute;
      top: 80px;
      width: calc(100% + 40px);
      height: calc(100% - 70px - 12px - 50px);
      border: 1px solid $bgColor5;
      border-top-left-radius: 56px;
      border-bottom-left-radius: 10px;
      border-top-right-radius: 10px;
      border-bottom-right-radius: 56px;
      margin-left: -20px;
      &.no-data {
        width: 100%;
        height: 40px;
        line-height: 40px;
        text-align: center;
        color: $bgColor9;
      }
      .no-data {
        height: 40px;
        line-height: 40px;
      }
    }
  }
}
.block4 {
  .description {
    width: 1108px;
    line-height: 30px;
    padding-bottom: 40px;
    margin: 0 auto 50px;
    text-indent: 25px;
    font-size: 22px;
    color: #000;
  }
}
.more {
  cursor: pointer;
}
.spn {
  font-size: 20px;
}
.references {
  .references-cont {
    display: flex;
    flex-wrap: wrap;
    p {
      padding-bottom: 25px;
      line-height: 40px;
      // text-indent: 25px;
      font-size: 22px;
      color: #333;
